body, p, ul {
  margin: 0;
  padding: 0;
}

.container {
border-top: 1px solid #e2e6e9;
}

.content {
  max-width: 1200px;
  margin: 0 auto;
  padding: 32px 16px;

  @media (min-width: 640px) {
    padding: 32px 24px;
  }

  @media (min-width: 1200px) {
    padding: 32px;
  }
}

.footer {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 32px;
  font-size: 12px;
  font-weight: 800;
  color: #89939A;

  @media (min-width: 640px) {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }

  &_list {
    display: flex;
    flex-direction: column;
    text-transform: uppercase;
    list-style: none;
    gap: 16px;

    @media (min-width: 640px) {
    gap: 16px;
    flex-direction: row;
    }

    @media (min-width: 1200px) {
    gap: 100px;
    flex-direction: row;
    }
  }

  &_link {
    text-decoration: none;
    color: #89939A;
  }

  &_top_button {
    display: flex;
    align-items: center;
    gap: 16px;
    align-self: center;
  }

  &_arrow_up {
    height: 32px;
    width: 32px;
    display: inline-block;
    background: url("../../../public/img/buttons/slider_button_default_up.svg") no-repeat;
    transform: rotate(90);

    &:hover {
    background: url("../../../public/img/buttons/slider_button_hover_up.svg") no-repeat;
    }
  }
}
